import React, { Component } from 'react';
import axios from 'axios';


class CrudPosts extends Component {
    constructor(props) {
        super(props);
        this.state = { 
            posts: [],
            userId: "",
            id: "",
            title: "",
            body: ""
         }
    }

    getPosts = async () => {
        try {
            const {data} = await axios.get('https://jsonplaceholder.typicode.com/posts');
            this.setState({
                posts: data
            })
        } catch(err) {
            console.error(err);
        }
    }

    createpost = async () => {
        try {
            const {data: postdata} = await axios.post('https://jsonplaceholder.typicode.com/posts', {
                userId: this.state.userId,
                title: this.state.title,
                body: this.state.body   
            })
            const posts = [...this.state.posts];
            posts.push(postdata);
            this.setState({
                posts
            })
            this.resetform();
        } catch(err) {
            console.error(err);
        }
    }

    resetform = () => {
        this.setState({
            userId: "",
            id: "",
            title: "",
            body: ""
        })
    }

    componentDidMount() {
        this.getPosts()
    }

    handleDelete = (x) => {
        const confirmbox = window.confirm("Do you want to delete this");
            if (confirmbox) {
            this.setState({
                posts: this.state.posts.filter(post => post.id !== x)
            })
        }
    }

    handleUpdate = (x) => {
        console.log(x);
        this.setState({
            userId: x.userId,
            id: x.id,
            title: x.title,
            body: x.body
        })
    }

    updatepost = async () => {
        const {id, userId, body, title} = this.state;
        try {
            const {data: posts_data} = await axios.put(`https://jsonplaceholder.typicode.com/posts/${id}`,{
                userId,
                title,
                body
            })
            const posts = [...this.state.posts];
            const index = posts.findIndex((posts_data) => posts_data.id === this.state.id);
            posts[index] = posts_data;
            this.setState({
                posts,
                userId: "",
                id: "",
                title: "",
                body: ""  
            })
            // this.resetform();
        } catch(err) {
            console.error(err);
        }
    }

    handleformsubmit = (e) => {
        console.log(this.state.id)
        e.preventDefault();
        if (this.state.id) {
            this.updatepost();
        } else this.createpost();
    }

    handleonchange = ({target: {name, value}}) => {
        this.setState({
            [name]: value
        })
    }

    render() { 
        if (this.state.posts.length ===0) {
            return <h1>No Tables to Show</h1>
        } else return (
            <>
            <div className = "form-div">
                <form onSubmit={this.handleformsubmit}>
                    <div className="form-group">
                        <label >USER ID</label>
                        <input type="text" name="userId" className="form-control" value={this.state.userId} onChange={this.handleonchange} placeholder="Enter User ID"/>
                    </div>
                    <div className="form-group">
                        <label>TITLE</label>
                        <input type="text" name="title" className="form-control" value={this.state.title} onChange={this.handleonchange} placeholder="Enter Title"/>
                    </div>
                    <div className="form-group">
                        <label>BODY</label>
                        <input type="text" name="body" className="form-control" value={this.state.body} onChange={this.handleonchange} placeholder="Enter Body"/>
                    </div>
                    <button type="submit" className="btn btn-primary">Submit</button>
                </form>
            </div>

            <hr/>

            <table className="table table-dark">
                <thead>
                    <tr>
                    <th scope="col">ID</th>
                    <th scope="col">USERID</th>
                    <th scope="col">TITLE</th>
                    <th scope="col">BODY</th>
                    <th scope="col">ACTION</th>
                    </tr>
                </thead>
                <tbody>
                    {this.state.posts.map(post=> (
                        <tr key={post.id}>
                            <td>{post.id}</td>
                            <td>{post.userId}</td>
                            <td>{post.title}</td>
                            <td>{post.body}</td>
                            <td>
                                <button className="btn btn-danger m-1" onClick={() => this.handleDelete(post.id)}>DELETE</button>
                                <button className="btn btn-warning m-1" onClick={() => this.handleUpdate(post)}>UPDATE</button>
                            </td>
                        </tr>
                    ))}
                    
                </tbody>
            </table>
            </>
          );
    }
}
 
export default CrudPosts;